import axios from "axios"
import BASE_URL from "../Config"


const getComplaintsList = async (token, pageNumber) => {
    try {
        const res = await axios.get(`${BASE_URL}viewComplaint?pageSize=10&pageNumber=${pageNumber}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const addReplyOfComplaint = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}complaint-change-status`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getComplaintsList, addReplyOfComplaint }