import axios from "axios"
import BASE_URL from "../Config"


const getNotificationList = async (token, pageNumber) => {
    try {
        const res = await axios.get(`${BASE_URL}notification-list?pageSize=10&pageNumber=${pageNumber}`, { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const sendNotification = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}notification-send`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

const getSearchList = async (token, data) => {
    try {
        const res = await axios.post(`${BASE_URL}notification-customer-search`, (data), { headers: { "Authorization": `Bearer ${token}` } })
        return res.data
    } catch (error) {
        console.log(error)
        throw (error)
    }
}

export { getNotificationList, sendNotification, getSearchList }