import { Image, Modal } from 'antd'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import ReactPaginate from 'react-paginate'
import { useSelector } from 'react-redux'
import BASE_URL from '../API/Config'
import { addReplyOfComplaint, getComplaintsList } from '../API/ManageComplaint'
import Container from '../Component/Container'
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import { toast, Toaster } from 'react-hot-toast'
import { useNavigate } from 'react-router-dom'

const AddedReplyToast = () => {
  // toast.success("Customer Data Added Successfully")
  toast.success('Reply Data Added Successfully.', {
    style: {
      border: '1px solid #1a48aa',
      padding: '16px',
      color: '#1a48aa',
    },
    iconTheme: {
      primary: '#1a48aa',
      secondary: '#FFFAEE',
    },
  });
}

const ManageCompaint = () => {
  const [complian, setCompain] = useState([])
  const [pageNumber, setPagenumber] = useState(1)
  const [totalSize, setTotalSize] = useState(10)
  const [changeStatus, setChangeStatus] = useState();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [Id, setId] = useState();
  const [example, setExample] = useState(false)
  const token = localStorage.getItem('admin_token')
  const navigate = useNavigate();

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);

  };
  // const token = useSelector(state => state.admin.adminlogintoken)

  const CustomerComplain = async () => {
    try {
      const total = await getComplaintsList(token, pageNumber)
      console.log("getComplaintsList", total)
      setCompain(total.data.rows)
      setExample(true)
      const Count = total.data.count
      setTotalSize(Count / 10)
    } catch (error) {
      console.log("getComplaintsList", error)
    }
  }

  const SignupSchema = Yup.object().shape({
    admin_reply: Yup.string()
      .min(2, 'State should be minimum 2 character')
      .required('Required'),

  });

  useEffect(() => {
    CustomerComplain()
  }, [pageNumber])

  const Click = async (d) => {
    window.scrollTo(0, 0);
    var CurrentPage = d.selected + 1;
    setPagenumber(CurrentPage)
  };

  const customersDetails = (id) => {
    navigate("/Customerdetails", { state: id })
  }
  const serviceProviderDetails = (data) => {
    navigate(`/Serviceproviderdetails`, { state: data })
  }

  return (
    <Container>
      <Toaster
        position="top-right"
        reverseOrder={false}
      />
      <Helmet>
        <meta charSet="utf-8" />
        <title>Car Washing | Manage Complaint</title>
      </Helmet>
      <div className="page-wrapper">
        <div className="page-header d-print-none">
          <div className="container-xl">
            <div className="row g-2 align-items-center">
              <div className="col">
                <h2 className="page-title">
                  Manage Complaints
                </h2>
              </div>
              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Tasks</h3>
                  </div>

                  <div className="table-responsive">
                    <table className="table card-table table-vcenter text-nowrap datatable" style={{ textTransform: 'capitalize'}}>
                      <thead>
                        <tr>
                          <th>#</th>
                          <th>Image</th>
                          <th>Customer Deatails</th>
                          <th>Service Provider Deatails</th>
                          <th>Description</th>
                          <th>Status</th>
                          <th>Admin Reply</th>
                          <th>Created Date</th>
                          <th></th>
                          {/* <th>Updated Reply</th> */}
                        </tr>
                      </thead>
                      {/* Updated Reply */}
                      {complian.length > 0 && complian.map((option, index) => {
                        var timezone = new Date(option.created_at).toLocaleString(undefined, { timeZone: 'Asia/Kolkata', hour12: true });
                        return (
                          <tbody key={index} >
                            <tr>
                              <td>{index + 1}</td>
                              <td><Image src={`https://tgu90.com:8000/complaintImg/${option.image}`} alt='...' width={60} height={60} /></td>
                              <td style={{ color: '#1a48aa', cursor: 'pointer' }} onClick={() => customersDetails(option.customer)}>{option.customer.fullName}
                                <div>{option.service.email}</div>
                                <div>{option.service.phone_num}</div></td>
                              <td style={{ color: '#1a48aa', cursor: 'pointer' }} onClick={() => serviceProviderDetails(option.service)}>{option.service.name}
                                <div>{option.service.email}</div>
                                <div>{option.service.phone_num}</div>
                              </td>
                              <td>{option.description}</td>
                              <td>{option.status}</td>
                              <td>{option.admin_reply}</td>
                              <td style={{ textTransform: 'uppercase' }}>{timezone}</td>
                              <td>
                                {option.status == 'Resolve' ?
                                  <span className='not-allowed'>
                                    <button className='btn me-auto text-light' disabled style={{ backgroundColor: '#1a48aa' }}>Update Reply</button>
                                  </span>
                                  :
                                  <button className='btn me-auto text-light' style={{ backgroundColor: '#1a48aa' }} onClick={() => { showModal(); setChangeStatus(option.status); setId(option.id) }} >Update Reply</button>
                                }
                              </td>
                            </tr>
                          </tbody>
                        )
                      })}
                      {example == true && complian.length == 0 &&
                        <tbody>
                          <tr>
                            <td colSpan={7}>
                              <div className="empty">
                                <div className="empty-img"><img src="bluescarwash.png" height="128" alt="" />
                                </div>
                                <p className="empty-subtitle text-muted">No Data Available In Table</p>
                                <div className="empty-action">
                                  {/* <a href="./." class="btn btn-primary">
                                  <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M12 5l0 14" /><path d="M5 12l14 0" /></svg>
                                  Add your first client
                                </a> */}
                                </div>
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      }
                    </table>
                  </div>
                </div>
              </div>
            </div>
            {example == true && complian.length > 0 &&
              <ReactPaginate
                style={{ background: 'red' }}
                previousLabel={"Previous"}
                nextLabel={'Next'}
                breakLabel={"..."}
                pageCount={totalSize}
                marginPagesDisplayed={3}
                pageRangeDisplayed={3}
                onPageChange={Click}
                containerClassName={'pagination  justify-content-center m-3'}
                pageClassName={'page-item'}
                pageLinkClassName={'page-link'}
                previousClassName={'page-item'}
                previousLinkClassName={'page-link'}
                nextClassName={'page-item'}
                nextLinkClassName={'page-link'}
                breakClassName={"page-item"}
                breakLinkClassName={'page-link'}
                activeClassName={'active'}
              />
            }
          </div>
        </div>
      </div>
      <Modal title="Add Reply" className='addmodal' closable={false} open={isModalOpen} onOk={handleOk} /* onCancel={() => { handleCancel() }} */ footer={null} width={600} style={{ backdropFilter: 'blur(4px)' }} centered>
        <hr className='hr' />
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <Formik
              initialValues={{
                admin_reply: '',
              }}
              validationSchema={SignupSchema}
              onSubmit={async (value, { resetForm }) => {
                let data = {
                  id: Id,
                  status: changeStatus,
                  admin_reply: value.admin_reply,
                }
                console.log(value)
                try {
                  const total = await addReplyOfComplaint(token, data)
                  console.log('addReplyOfComplaint', total);
                  setIsModalOpen(false);
                  if (total.status === 200) {
                    resetForm({ values: '' });
                  }
                  CustomerComplain();
                  AddedReplyToast();
                } catch (error) {
                  console.log(error)
                }
              }}
            >
              {({ errors, touched, resetForm, value }) => (
                <Form noValidate >
                  <div className='row'>
                    <div className="col-lg-6" style={{ margin: '5px 20px' }}>
                      <label className="form-label">Change Status</label>
                      <div className="ml-2">
                        <select className="form-select" /* style={{ borderColor: 'rgb(0 0 0 / 21%)' }} */ value={changeStatus} onChange={(e) => setChangeStatus(e.target.value)}>
                          <option value="Resolve"> Resolve</option>
                          <option value="Revert Payment">Revert Payment</option>
                          <option value="Pending">Pending</option>
                          <option value="Cancelled">Cancelled</option>
                        </select>
                      </div>
                    </div>
                    <div className="col-lg-12" style={{ padding: '0px 24px' }}>
                      <div className="m-2">
                        <label className="form-label">Reply</label>
                        <Field as="textarea" name="admin_reply" className="form-control" rows="4" placeholder="Enter Reply" />
                      </div>
                      {errors.admin_reply && touched.admin_reply ? (
                        <div style={{ color: '#9D0305', fontSize: '14px', margin: '8px' }}>{errors.admin_reply}</div>
                      ) : null}
                    </div>
                  </div>
                  <div className="modal-footer">
                    <div className="col-lg-6" >
                      <button type="button" className="btn me-auto w-50" onClick={() => { setIsModalOpen(false); resetForm({ value: '' }) }} style={{ backgroundColor: '#1a48aa', color: '#ffffff' }}>Close</button>
                      <button type="submit" className="btn w-50" style={{ backgroundColor: '#1a48aa', color: '#ffffff' }} > Add Reply </button>
                    </div>
                  </div>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </Modal>
    </Container>
  )
}

export default ManageCompaint
