const NotificationSend = () => {
    // toast.success("Customer Data Added Successfully")
    toast.success("Notification send successfully", {
        style: {
            border: '1px solid #1a48aa',
            padding: '16px',
            color: '#1a48aa',
        },
        iconTheme: {
            primary: '#1a48aa',
            secondary: '#FFFAEE',
        },
    });
}

const NotificationSendError = (message) => {
    // toast.success("Customer Data Added Successfully")
    toast.error(message, {
        style: {
            border: '1px solid #1a48aa',
            padding: '16px',
            color: '#1a48aa',
        },
        iconTheme: {
            primary: '#1a48aa',
            secondary: '#FFFAEE',
        },
    });
}

