import { Switch } from 'antd'
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet'
import { toast, Toaster } from 'react-hot-toast'
import ReactPaginate from 'react-paginate'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import BASE_URL from '../API/Config'
import { changeStatus, getContactUsList } from '../API/ContactUs'
import Container from '../Component/Container'

const Contactus = () => {
  const [viewdata, setViewData] = useState([])
  const [pageData, setPageData] = useState(0)
  const [totalSize, setTotalSize] = useState(10)
  const [pageNumber, setPagenumber] = useState(1)
  const [example, setExample] = useState(false)
  const [toggle, setToggle] = useState(true)

  const token = localStorage.getItem('admin_token')

  const changeStatusToast = () => {
    toast.success('Status Changed Successfully.', {
      style: {
        border: '1px solid #1a48aa',
        padding: '16px',
        color: '#1a48aa',
      },
      iconTheme: {
        primary: '#1a48aa',
        secondary: '#FFFAEE',
      },
    });
  }
  const getAllContactUs = async () => {
    try {
      const total = await getContactUsList(token, totalSize, pageNumber)
      console.log('getContactUsList', total)
      setViewData(total.data.rows)
      setExample(true)
      const Count = total.data.count
      setTotalSize(Count / 10)

    } catch (error) {
      console.log('getContactUsList', error)
    }
  }
  const contact = async () => {
    const view = await axios.get(`${BASE_URL}contact_us?pageSize=10&pageNumber=1`, {
      headers: {
        "Authorization": `Bearer ${token}`
      }
    })
    console.log(view)
    setViewData(view.data.data.rows)
    const Count = view.data.data.count
    console.log(Count)
    setPageData(Math.ceil(Count / 10));
  }

  // const fecthComments = async (CurrentPage) => {
  //   const res = await fetch(
  //     `${BASE_URL}contact_us?pageSize=10&pageNumber=${CurrentPage}`, {
  //     headers: {
  //       "Authorization": `Bearer ${token}`
  //     },

  //     data: {
  //       pageSize: 10,
  //       pageNumber: CurrentPage
  //     }
  //   }
  //   );
  //   const data = await res.json();
  //   console.log(data)
  //   const d = data.data.rows;
  //   return d;
  // }
  const Click = async (d) => {
    window.scrollTo(0, 0);
    var CurrentPage = d.selected + 1;
    setPagenumber(CurrentPage)
    // const Comments = await fecthComments(CurrentPage);
    // setViewData(Comments)
  };

  const Registration = async (id, e) => {
    // let event;
    console.log(id, e)
    // if (e === true) {

    // } else {
    //   event = 'closed'
    // }
    let data = {
      "id": id,
      "status": e == true ? 'open' : 'closed',
    }
    console.log(data);
    try {
      const total = await changeStatus(token, data)
      console.log('changeStatus', total)
      changeStatusToast();
    } catch (error) {
      console.log(error)
    }
  }
  useEffect(() => {
    getAllContactUs();
  }, [pageNumber])



  return (
    <Container>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Car Washing | Contact Us</title>
      </Helmet>
      <Toaster
        position="top-right"
        reverseOrder={false}
      />
      <div className="page-wrapper">
        <div className="page-header d-print-none">
          <div className="container-xl">
            <div className="row g-2 align-items-center">
              <div className="col">
                <h2 className="page-title">
                  Contact Us
                </h2>
              </div>

              <div className="col-12">
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Contact Us Data</h3>
                  </div>
                  <div className="table-responsive ">
                    <table className="table card-table table-vcenter text-nowrap datatable " style={{ textTransform: 'capitalize' }}>
                      <thead>
                        <tr>
                          <th>#</th>
                          <th>Full Name</th>
                          <th>Contact Number</th>
                          <th>Message</th>
                          <th>Status</th>
                        </tr>
                      </thead>
                      {viewdata.length > 0 && viewdata.map((data, index) => {
                        let serial_num = ((10 * (pageNumber - 1)) + index + 1);

                        return (
                          <tbody key={index}>
                            <tr >
                              <td>{serial_num}</td>
                              <td> {data.first_name} {data.last_name}</td>
                              <td>{data.contact_number}</td>
                              <td>{data.message}</td>
                              <td>
                                {data.status == 'open' &&
                                  <div className="col-auto ms-auto d-print-none">
                                    <Switch checkedChildren="Open" onClick={(e) => Registration(data.id, e)} unCheckedChildren="Close" defaultChecked
                                      onChange={(e) => setToggle(e)} /* style={{ backgroundColor: toggle === true ? 'green' : 'red' }} */ />
                                    <span>
                                    </span>
                                  </div>
                                }
                                {data.status == 'closed' &&
                                  <div className="col-auto ms-auto d-print-none">
                                    <Switch checkedChildren="Open" onClick={(e) => Registration(data.id, e)} unCheckedChildren="Close"
                                      onChange={(e) => setToggle(e)} /* style={{ backgroundColor: '#1a48aa' }} */ />
                                    <span>
                                    </span>
                                  </div>
                                }
                              </td>
                            </tr>
                          </tbody>
                        )
                      })}
                      {example == true && viewdata.length == 0 &&
                        <tbody>
                          <tr>
                            <td colSpan={7}>
                              <div className="empty">
                                <div className="empty-img"><img src="bluescarwash.png" height="128" alt="" />
                                </div>
                                <p className="empty-subtitle text-muted">No Data Available In Table</p>
                                <div className="empty-action">
                                  {/* <a href="./." class="btn btn-primary">
                                  <svg xmlns="http://www.w3.org/2000/svg" class="icon" width="24" height="24" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor" fill="none" stroke-linecap="round" stroke-linejoin="round"><path stroke="none" d="M0 0h24v24H0z" fill="none" /><path d="M12 5l0 14" /><path d="M5 12l14 0" /></svg>
                                  Add your first client
                                </a> */}
                                </div>
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      }
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {example == true && viewdata.length > 0 &&
        <ReactPaginate

          previousLabel={"Previous"}
          nextLabel={'Next'}
          breakLabel={"..."}
          pageCount={totalSize}
          marginPagesDisplayed={3}
          pageRangeDisplayed={3}
          onPageChange={Click}
          containerClassName={'pagination  justify-content-center mt-3'}
          pageClassName={'page-item'}
          pageLinkClassName={'page-link'}
          previousClassName={'page-item'}
          previousLinkClassName={'page-link'}
          nextClassName={'page-item'}
          nextLinkClassName={'page-link'}
          breakClassName={"page-item"}
          breakLinkClassName={'page-link'}
          activeClassName={'active'}
        />
      }

    </Container>
  )
}

export default Contactus
